import emailjs from '@emailjs/browser';

// EmailJS konfigrasyonu - .env.local dosyasndan okunur
const EMAILJS_CONFIG = {
  serviceId: process.env.REACT_APP_EMAILJS_SERVICE_ID,
  templateId: process.env.REACT_APP_EMAILJS_TEMPLATE_ID,
  publicKey: process.env.REACT_APP_EMAILJS_PUBLIC_KEY
};

// Konfigrasyon kontrol
const validateConfig = () => {
  console.log(' Validating EmailJS Config:', {
    serviceId: EMAILJS_CONFIG.serviceId,
    templateId: EMAILJS_CONFIG.templateId,
    publicKey: EMAILJS_CONFIG.publicKey,
    serviceIdLength: EMAILJS_CONFIG.serviceId?.length,
    templateIdLength: EMAILJS_CONFIG.templateId?.length,
    publicKeyLength: EMAILJS_CONFIG.publicKey?.length
  });

  const missing = [];
  if (!EMAILJS_CONFIG.serviceId) missing.push('REACT_APP_EMAILJS_SERVICE_ID');
  if (!EMAILJS_CONFIG.templateId) missing.push('REACT_APP_EMAILJS_TEMPLATE_ID');
  if (!EMAILJS_CONFIG.publicKey) missing.push('REACT_APP_EMAILJS_PUBLIC_KEY');
  
  if (missing.length > 0) {
    console.error(' EmailJS konfigrasyonu eksik! .env.local dosyasna u deerleri ekleyin:');
    missing.forEach(key => console.error(`- ${key}`));
    return false;
  }
  return true;
};

// EmailJS'i balat
export const initEmailJS = () => {
  if (!validateConfig()) {
    return false;
  }
  emailjs.init(EMAILJS_CONFIG.publicKey);
  console.log(' EmailJS baaryla balatld');
  return true;
};

// E-posta gnderme fonksiyonu
export const sendContactEmail = async (formData) => {
  // Konfigrasyon kontrol
  if (!validateConfig()) {
    return { 
      success: false, 
      message: 'E-posta sistemi yaplandrlmam. Ltfen sistem yneticisiyle iletiime gein.' 
    };
  }

  try {
    // Debug: Konfigrasyon bilgilerini logla
    console.log(' EmailJS Config Debug:', {
      serviceId: EMAILJS_CONFIG.serviceId,
      templateId: EMAILJS_CONFIG.templateId,
      publicKey: EMAILJS_CONFIG.publicKey ? 'Mevcut' : 'Eksik'
    });

    // Template iin parametreleri hazrla
    const templateParams = {
      from_name: formData.name,
      from_email: formData.email || 'Belirtilmemi',
      from_phone: formData.phone || 'Belirtilmemi',
      service_type: formData.service === 'soru' ? 'Soru Sorma' : 'Hal Siparii',
      message: formData.message || 'Mesaj belirtilmemi',
      address: formData.address || 'Belirtilmemi',
      to_email: 'gulumserhaliyikama@gmail.com',
      subject: formData.service === 'soru' 
        ? `Soru - ${formData.name}` 
        : `Hal Siparii - ${formData.name}`
    };

    console.log(' E-posta gnderiliyor...', templateParams);

    const response = await emailjs.send(
      EMAILJS_CONFIG.serviceId,
      EMAILJS_CONFIG.templateId,
      templateParams,
      EMAILJS_CONFIG.publicKey
    );

    console.log('E-posta baaryla gnderildi:', response);
    return { success: true, message: 'E-posta baaryla gnderildi!' };

  } catch (error) {
    console.error(' E-posta gnderim hatas:', error);
    console.error(' Hata detaylar:', {
      status: error.status,
      text: error.text,
      message: error.message,
      fullError: error
    });
    
    let userMessage = 'E-posta gnderilirken bir hata olutu. ';
    
    if (error.status === 404) {
      userMessage += 'Service veya Template bulunamad. Konfigrasyonu kontrol edin.';
    } else if (error.status === 401) {
      userMessage += 'Yetkilendirme hatas. Public Key\'i kontrol edin.';
    } else if (error.status === 400) {
      userMessage += 'Geersiz parametreler. Template formatn kontrol edin.';
    } else {
      userMessage += 'Ltfen tekrar deneyin.';
    }
    
    return { 
      success: false, 
      message: userMessage 
    };
  }
};

