import React from 'react';
import { motion } from 'framer-motion';
import styles from './ServiceAreas.module.css';

const ServiceAreas = () => {
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        duration: 0.5
      }
    }
  };

  // le ve mahalleler
  const serviceAreas = [
    {
      district: 'Esenler',
      isMainCenter: true,
      neighborhoods: [
        'Atalan', 'iftehavuzlar', 'Fevzi akmak', 'Havaalan', 
        'Kemer', 'Menderes', 'Namk Kemal', 'Nine Hatun', 
        'Orureis', 'Tuna', 'Turgutreis', 'Yavuz Selim'
      ]
    },
    {
      district: 'Gngren',
      neighborhoods: [
        'Abdurrahman Nafiz Grman', 'Akaburgaz', 'Geit',
        'Gnetepe', 'Haznedar', 'Mareal Fevzi akmak',
        'Mehmet Nesih zmen', 'Merkez', 'Tozkoparan'
      ]
    },
    {
      district: 'Baclar',
      neighborhoods: [
        'Balar', 'Demirkap', 'Evren', 'Fatih', 'Gztepe',
        'Gneli', 'nn', 'Kemalpaa', 'Kirazl', 'Mahmutbey',
        'Merkez', 'Sakzaac', 'Sancaktepe', 'Yldztepe', 'Yzyl'
      ]
    },
    {
      district: 'Bayrampaa',
      neighborhoods: [
        'Altntepsi', 'Cevatpaa', 'Kartaltepe', 'Muratpaa',
        'Ortamahalle', 'Ruhupaa', 'Terazidere', 'Vatan', 'Yldrm'
      ]
    },
    {
      district: 'Zeytinburnu',
      neighborhoods: [
        'Betelsiz', 'rpc', 'Gkalp', 'Merkezefendi',
        'Nuripaa', 'Smer', 'Telsiz', 'Veliefendi', 'Yenidoan'
      ]
    },
    {
      district: 'Bahelievler',
      neighborhoods: [
        'Adnan Kahveci', 'Bahelievler', 'Cumhuriyet', 'Erturulgazi',
        'Fevzi akmak', 'Hrriyet', 'Kocasinan', 'Sirinevler',
        'Soanl', 'irinevler', 'Yenibosna', 'Zafer'
      ]
    }
  ];

  return (
    <div className={styles.container}>
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
        className={styles.header}
      >
        <h1>Hizmet Verdiimiz Blgeler</h1>
        <p>Esenler merkezli olarak evre ilelerde cretsiz alma-teslim hizmeti sunuyoruz. Tm mahallelerimize profesyonel hal ykama hizmeti veriyoruz.</p>
      </motion.div>

      <motion.div
        variants={containerVariants}
        initial="hidden"
        animate="visible"
        className={styles.areasGrid}
      >
        {serviceAreas.map((area, index) => (
          <motion.div
            key={index}
            variants={itemVariants}
            whileHover={{ scale: 1.02, y: -5 }}
            whileTap={{ scale: 0.98 }}
            className={`${styles.areaCard} ${area.isMainCenter ? styles.mainCenter : ''}`}
          >
            <div className={styles.cardHeader}>
              <div className={styles.districtInfo}>
                <span className={styles.districtIcon}>
                  {area.isMainCenter ? '' : ''}
                </span>
                <h2>{area.district}</h2>
                {area.isMainCenter && (
                  <span className={styles.mainBadge}>Ana Merkez</span>
                )}
              </div>
              <span className={styles.neighborhoodCount}>
                {area.neighborhoods.length} Mahalle
              </span>
            </div>

            <div className={styles.neighborhoodsList}>
              {area.neighborhoods.map((neighborhood, idx) => (
                <span key={idx} className={styles.neighborhood}>
                  {neighborhood}
                </span>
              ))}
            </div>

            <div className={styles.cardFooter}>
              <div className={styles.serviceInfo}>
                <span className={styles.serviceIcon}></span>
                <span>cretsiz Alma-Teslim</span>
              </div>
              <a href="tel:+905313842496" className={styles.callButton}>
                <span></span>
                <span>Ara</span>
              </a>
            </div>
          </motion.div>
        ))}
      </motion.div>

      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, delay: 0.8 }}
        className={styles.serviceNote}
      >
        <div className={styles.noteCard}>
          <h3> zel Hizmet Bilgileri</h3>
          <div className={styles.noteGrid}>
            <div className={styles.noteItem}>
              <span className={styles.noteIcon}></span>
              <h4>cretsiz Tama</h4>
              <p>Tm listelenen blgelerde cretsiz evden alma ve teslim hizmeti</p>
            </div>
            <div className={styles.noteItem}>
              <span className={styles.noteIcon}></span>
              <h4>Hzl Teslimat</h4>
              <p>1 hafta iinde temizlenmi hallarnz teslim ediyoruz</p>
            </div>
            <div className={styles.noteItem}>
              <span className={styles.noteIcon}></span>
              <h4>7/24 Destek</h4>
              <p>WhatsApp hattmzdan her zaman bize ulaabilirsiniz</p>
            </div>
          </div>
          <div className={styles.extraAreas}>
            <h4> Dier Blgeler</h4>
            <p>Listelenmemi blgeler iin ltfen bizimle iletiime gein. Size zel zm retmekten memnuniyet duyarz.</p>
            <div className={styles.contactActions}>
              <a href="tel:+905313842496" className={styles.primaryBtn}>
                <span></span>
                <span>Hemen Ara</span>
              </a>
              <a href="https://wa.me/905313842496" className={styles.secondaryBtn} target="_blank" rel="noopener noreferrer">
                <span></span>
                <span>WhatsApp</span>
              </a>
            </div>
          </div>
        </div>
      </motion.div>
    </div>
  );
};

export default ServiceAreas; 