// EmailJS test fonksiyonlar
import { sendContactEmail, initEmailJS } from '../services/emailService';

export const testEmailConfiguration = () => {
  console.log(' EmailJS Konfigrasyon Testi Balatlyor...');
  
  // Environment variables kontrol
  const config = {
    serviceId: process.env.REACT_APP_EMAILJS_SERVICE_ID,
    templateId: process.env.REACT_APP_EMAILJS_TEMPLATE_ID,
    publicKey: process.env.REACT_APP_EMAILJS_PUBLIC_KEY,
    contactEmail: process.env.REACT_APP_CONTACT_EMAIL
  };
  
  console.log(' Konfigrasyon Durumu:');
  console.log('Service ID:', config.serviceId ? ' Tanml' : ' Eksik');
  console.log('Template ID:', config.templateId ? ' Tanml' : ' Eksik');
  console.log('Public Key:', config.publicKey ? ' Tanml' : ' Eksik');
  console.log('Contact Email:', config.contactEmail ? ' Tanml' : ' Eksik');
  
  // EmailJS balatma testi
  const initResult = initEmailJS();
  console.log('EmailJS Init:', initResult ? ' Baarl' : ' Baarsz');
  
  return {
    config,
    initResult,
    allConfigured: !!(config.serviceId && config.templateId && config.publicKey)
  };
};

export const sendTestEmail = async () => {
  console.log(' Test E-postas Gnderiliyor...');
  
  const testData = {
    name: 'Test Kullanc',
    email: 'test@example.com',
    phone: '05313842496',
    service: 'soru',
    message: 'Bu bir test mesajdr. EmailJS entegrasyonu alyor mu?',
    address: ''
  };
  
  try {
    const result = await sendContactEmail(testData);
    console.log('Test e-posta sonucu:', result);
    return result;
  } catch (error) {
    console.error('Test e-posta hatas:', error);
    return { success: false, message: error.message };
  }
};

// Browser console'dan altrma iin window'a ekle
if (typeof window !== 'undefined') {
  window.testEmailJS = {
    testConfig: testEmailConfiguration,
    sendTest: sendTestEmail
  };
  console.log(' EmailJS Test Fonksiyonlar Hazr!');
  console.log('Konsola u komutlar yazabilirsiniz:');
  console.log('- window.testEmailJS.testConfig() : Konfigrasyonu test et');
  console.log('- window.testEmailJS.sendTest() : Test e-postas gnder');
} 